import React, {
  useCallback, useEffect, useMemo, useState
} from 'react';
import {
  Box, Button, FormControl, FormLabel, Input, Select
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { format, subYears } from 'date-fns';
import MiniBox from '../../../../layout/MiniBox';
import { personDetails as personDetailsType } from '../../../../types/user';
import { usePersonState } from '../../../../hooks/user';
import DatePicker from '../../../../components/DatePicker';
import { useAppAsyncDispatch } from '../../../../hooks/store';
import { updatePerson as updatePersonApi } from '../../../../store/middlewares/user';
import { apiErrorHandler, documentTypes } from '../../../../utils/general';
import SsnInput from '../../../../components/SsnInput';

/**
 * Component for collecting the details of the person.
 *
 * @component
 * @module KYC
 * @link {@see https://www.figma.com/file/ndxr4b9PADQG9FMkIWp2ie/For-Solid-(Product)?node-id=22252%3A122634}
 * @returns {JSX.Element}
 */
const PersonalDetails = () => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const dispatch = useAppAsyncDispatch();

  const [personDetails, updatePerson] = usePersonState();
  const [isSSNValid, updateSSNValidation] = useState(false);

  useEffect(() => {
    if (personDetails !== null) {
      if (personDetails.idNumber.length !== 0) { updateSSNValidation(true); }

      if (personDetails.idType.length === 0) {
        updatePerson('idType', 'ssn');
      }
    }
  }, []);

  const isValid = useMemo(() => {
    /* eslint-disable no-useless-escape */
    const emailRegex = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w\w+)+$/;

    return (personDetails && personDetails?.firstName.length !== 0 && personDetails?.lastName.length !== 0 && personDetails?.email.length !== 0 && emailRegex.test(personDetails.email) && isSSNValid && personDetails?.idType.length !== 0);
  }, [personDetails, isSSNValid]);

  /**
   * Submit callback - onsuccess navigate to address screen
   */
  const submit = useCallback(() => {
    if (isValid && personDetails) {
      /**
       * Api call
       */
      dispatch(
        updatePersonApi(personDetails.id, {
          firstName: personDetails.firstName,
          lastName: personDetails.lastName,
          email: personDetails.email,
          dateOfBirth: personDetails.dateOfBirth,
          idNumber: personDetails.idNumber,
          idType: personDetails.idType
        })
      ).then(() => {
        /**
         * Navigate to person address screen
         */
        navigate('/ao/kyc/personAddress');
      }).catch(apiErrorHandler);
    }
  }, [isValid, personDetails]);

  /**
   * letter validation with state update
   */
  const allowLetterValidation = useCallback(
    (nameType: keyof personDetailsType, updatedName: string) => {
      if (/^[a-zA-Z\s]+$/.test(updatedName) || updatedName === '') {
        updatePerson(nameType, updatedName);
      }
    },
    [updatePerson],
  );

  return (
    <div className="page">
      <form
        onSubmit={(e) => {
          e.preventDefault();
          submit();
        }}
        autoComplete="off"
      >
        <MiniBox
          varient="box"
          heading={t('personal_details.title')}
          showBack={false}
          footer={(
            <Button data-testid="submit-btn" type="submit" width="100%" variant="primary" disabled={!isValid}>
              {t('personal_details.ctaText')}
            </Button>
          )}
        >
          <Box width="100%">
            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('personal_details.firstNameLabel')}
              </FormLabel>
              <Input data-testid="firstName" variant="primary" onChange={(e:any) => allowLetterValidation('firstName', e.target.value)} value={personDetails?.firstName} />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('personal_details.lastNameLabel')}
              </FormLabel>
              <Input data-testid="lastName" variant="primary" onChange={(e:any) => allowLetterValidation('lastName', e.target.value)} value={personDetails?.lastName} />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('personal_details.emailLabel')}
              </FormLabel>
              <Input data-testid="email" variant="primary" type="email" onChange={(e: any) => updatePerson('email', e.target.value)} value={personDetails?.email} />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('personal_details.dobLabel')}
              </FormLabel>
              <DatePicker
                maxDate={subYears(new Date(), 18)}
                dateFormat="MM / dd / yyyy"
                value={personDetails?.dateOfBirth}
                peekNextMonth
                showMonthDropdown
                showYearDropdown
                dropdownMode="select"
                onChange={(date) => {
                  if (date) { updatePerson('dateOfBirth', format(date, 'yyyy-MM-dd')); }
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('personal_details.documentType')}
              </FormLabel>
              <Select
                data-testid="document-type"
                variant="primary"
                value={personDetails?.idType}
                defaultValue="ssn"
                onChange={(e:any) => allowLetterValidation('idType', e.target.value)}
              >
                {
                  documentTypes.map((d) => (
                    <option key={d.type} value={d.type}>
                      {d.label}
                    </option>
                  ))
                }
              </Select>
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t(`personal_details.${personDetails?.idType}`, 'Social Security Number')}
              </FormLabel>
              <SsnInput
                isLast4={false}
                idType={personDetails?.idType}
                value={personDetails?.idNumber || ''}
                onChange={(_, v, validation) => {
                  updatePerson('idNumber', v);
                  updateSSNValidation(validation);
                }}
              />
            </FormControl>

          </Box>
        </MiniBox>
      </form>
    </div>
  );
};

export default PersonalDetails;
